
import { computed, defineComponent } from 'vue'
import { Positions } from '../../models'

export default defineComponent({
  name: 'it-drawer',
  props: {
    modelValue: Boolean,
    width: { type: String, default: '30vw' },
    closableMask: { type: Boolean, default: true },
    hideMask: Boolean,
    placement: {
      type: String,
      default: Positions.R,
      validator: (value: Positions) =>
        [Positions.L, Positions.R].includes(value),
    },
  },
  setup(props, { emit }) {
    function maskClick() {
      if (props.closableMask) {
        close()
      }
    }

    function close() {
      emit('update:modelValue', false)
    }

    const transitionSide = computed(() => {
      return props.placement === Positions.R ? 'left' : 'right'
    })

    return {
      maskClick,
      transitionSide,
    }
  },
})
