
import { defineComponent, watch, ref, onMounted } from 'vue'

export default defineComponent({
  name: 'it-loading-bar',
  data() {
    return {
      progress: 0,
      height: 2,
      start: false
    }
  },
  watch: {
    progress: function (newValue) {      
      if (newValue === 100) {
        setTimeout(() => {
          this.progress = 0
          this.height = 2
        }, 600)
      }
    }
  }
})
