
import {
  defineComponent,
  ref,
  provide,
  computed,
  onBeforeUpdate,
  nextTick,
  toRef,
} from 'vue'

import { Positions } from '../../models'
import clickoutside from '../../directives/clickOutside'
import usePopover from '../../api/usePopover'

export default defineComponent({
  name: 'it-select',
  directives: { clickoutside },
  props: {
    placement: { type: String, default: Positions.B },
    disabled: Boolean,
    divided: Boolean,
    filterable: Boolean,
    index: String,
    labelTop: String,
    placeholder: { type: String, default: 'Select' },
    options: { type: Array, default: [] },
    modelValue: { type: [String, Number] },
  },
  setup(props, { emit }) {
    const search = ref<string | number>('')
    const focusIndex = ref(-1)
    const optionsRefs = ref<HTMLElement[]>([])
    const listRef = ref<HTMLElement | null>(null)

    onBeforeUpdate(() => {
      optionsRefs.value = []
    })

    // Dropdown logic
    const { show, hidePopover, showPopover } = usePopover(props)

    function toggleList() {
      if (props.disabled) {
        return
      }
      if (show.value) {
        hidePopover()
      } else {
        unfocus()
        showPopover()
      }
    }

    // select logic
    function selectOption(value: string | number) {
      search.value = props.filterable ? value : ''
      emit('update:modelValue', value)
    }

    function unfocus() {
      if (focusIndex.value === -1) {
        return
      }
      focusIndex.value = -1
    }

    // const searched = computed(() => {
    //   return slots.default!().filter!((i) => i.$data.visible === true)
    // })

    async function handleKey(type: 'up' | 'down') {
      if (!show.value || !props.options.length) {
        return
      }

      if (type === 'up') {
        focusIndex.value =
          focusIndex.value <= 0
            ? props.options.length - 1
            : focusIndex.value - 1
      } else if (type === 'down') {
        focusIndex.value =
          focusIndex.value === props.options.length - 1
            ? 0
            : focusIndex.value + 1
      }

      // Scroll to focused element
      await nextTick()
      const selectedEl = optionsRefs.value.find((r) =>
        r.className.includes('focused')
      )
      if (selectedEl) {
        selectedEl.scrollIntoView({ block: 'nearest', inline: 'start' })
      }
    }

    function enterKey() {
      if (!show.value || focusIndex.value === -1) {
        return
      }
      const option: any = props.options[focusIndex.value]
      selectOption(props.index ? option[props.index] : option)
      hidePopover()
    }

    return {
      toggleList,
      // selected,
      selectOption,
      search,
      show,
      hidePopover,
      showPopover,
      handleKey,
      focusIndex,
      enterKey,
      optionsRefs,
    }
  },
})
