
import { defineComponent } from 'vue'
import ItIcon from '../icon'
import { Colors, Positions } from '../../models'

export default defineComponent({
  name: 'it-notification',
  components: {
    'it-icon': ItIcon,
  },
  data() {
    return {
      id: null,
      show: false,
      text: '',
      title: '',
      icon: '',
      emoji: '',
      duration: 5000,
      position: {} as { [key: string]: string },
      placement: Positions.TR,
      type: Colors.PRIMARY,
      image: '',
      timer: setTimeout(() => {}),
      onClose: () => {},
    }
  },
  computed: {
    backgroundImage() {
      // return this.image ? `url(${this.image})` : ''
    },
    typeColor() {
      if (this.emoji) {
        return '#fbfbfb'
      }
      return {
        primary: '#3051ff',
        success: '#07d85b',
        danger: '#F93155',
        warning: '#ffba00',
      }
    },
    positionPx() {
      const posPx: { [key: string]: string } = {}
      for (const key in this.position) {
        if (this.position.hasOwnProperty(key)) {
          posPx[key] = this.position[key] + 'px'
        }
      }
      return posPx
    },
    typeIcon() {
      return {
        primary: 'info_outline',
        success: 'done',
        warning: 'error_outline',
        danger: 'clear',
      }
    },
  },
  mounted() {
    this.startTimer()
  },
  methods: {
    destroy() {
      this.$el.parentNode!.removeChild(this.$el)
    },

    startTimer() {
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          this.show = false
          if (this.onClose) {
            this.onClose()
          }
        }, this.duration) as any
      }
    },

    clearTimer() {
      clearTimeout(this.timer)
    },
  },
})
