
import {
  computed,
  defineComponent,
  nextTick,
  onMounted,
  provide,
  ref,
} from 'vue'

export default defineComponent({
  name: 'it-tabs',
  props: {
    initialTab: Number,
    vertical: Boolean,
    box: Boolean,
  },
  setup(props, { slots }) {
    const selectedIndex = ref(0)
    const tabs = ref([])
    const tabsRefs = ref<HTMLElement[]>([])

    provide('tabs', tabs)

    onMounted(async () => {
      await nextTick()
      selectTab(props.initialTab || selectedIndex.value)
    })

    function selectTab(i: number) {
      // if (tabs.value[i].disabled) return

      // selectedIndex.value = i
      // tabs.value.forEach((tab, index) => {
      //   tab.isActive = index === i
      // })
    }

    function focusNextTab(i: number) {
      if (!tabs.value[i]) {
        focusNextTab(0)
        return
      }
      // if (tabs.value[i].disabled) {
      //   focusNextTab(i + 1)
      //   return
      // }

      tabsRefs.value[i].focus()
    }

    function focusPrevTab(i: number) {
      if (!tabs.value[i]) {
        focusPrevTab(tabsRefs.value.length - 1)
        return
      }
      // if (tabs.value[i].disabled) {
      //   focusPrevTab(i - 1)
      //   return
      // }

      tabsRefs.value[i].focus()
    }

    return {
      selectedIndex,
      tabs,
      selectTab,
      tabsRefs,
      focusNextTab,
      focusPrevTab,
    }
  },
})
